@media only screen and (max-width: 991px) { .aside { display: none; } }

@media only screen and (max-width: 767px) { .aside { margin-bottom: 30px; } }

.aside__block { padding: 20px 25px 45px; margin-bottom: 30px; box-shadow: 0px 0px 10px 0px rgba(0, 0, 1, 0.15); }

.aside-banner { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; display: flex; justify-content: flex-end; flex-direction: column; padding-left: 25px; padding-right: 25px; padding-bottom: 30px; padding-top: 120%; position: relative; }

@media only screen and (max-width: 767px) { .aside-banner { padding: 20px; padding-top: 30%; } }

@media only screen and (max-width: 575px) { .aside-banner { padding-top: 60%; } }

.aside-banner::before { content: ''; position: absolute; top: 0; left: 0; width: 100%; height: 100%; z-index: 0; background-image: linear-gradient(0deg, black 0%, rgba(0, 0, 0, 0) 100%); }

.aside-banner__block { position: relative; z-index: 1; }

.aside-banner__subttl { margin-bottom: 5px; }

.aside-banner__contact { font-size: 18px; text-transform: uppercase; color: #fff; font-family: 'helveticaneuecyr-bold', Arial, sans-serif; text-transform: uppercase; }

.aside-banner__btn { font-size: 0; padding-top: 12px; }

.aside-show-btn { font-size: 0; margin-bottom: 30px; display: none; }

@media only screen and (max-width: 991px) { .aside-show-btn { display: block; } }

.car-search__title { margin-bottom: 25px; }

.car-search__slider { margin-bottom: 20px; }

.pagination { font-size: 0; }

.pagination__list { display: inline-block; vertical-align: top; }

.pagination__item { display: inline-block; vertical-align: top; margin-right: 3px; margin-left: 3px; }

.pagination__link { color: #828282; display: block; text-align: center; background-color: #fff; border: 1px solid  #ddd; padding: 5px; height: 30px; min-width: 30px; font-size: 14px; }

.pagination__link.is-active, .no-touch .pagination__link:hover, .no-touch .pagination__prev:hover, .no-touch .pagination__next:hover { background-color: #414141; border-color: #414141; color: #fff; }

.pagination__link.has-dots { border: 0; pointer-events: none; padding-left: 0; padding-right: 0; min-width: 0; }

.pagination__prev, .pagination__next { display: inline-block; position: relative; vertical-align: top; width: 30px; height: 30px; border: 1px solid  #ddd; }

.pagination__prev::before, .pagination__next::before { content: ''; display: inline-block; width: 9px; height: 9px; border-left: 2px solid #ddd; border-bottom: 2px solid #ddd; transform: rotate(45deg); position: absolute; top: 10px; }

.pagination__prev { margin-right: 3px; }

.pagination__prev::before { width: 9px; height: 9px; border-left: 2px solid #ddd; border-bottom: 2px solid #ddd; transform: rotate(45deg); left: 11px; }

.pagination__next { margin-left: 3px; }

.pagination__next::before { width: 9px; height: 9px; border-left: 2px solid #ddd; border-bottom: 2px solid #ddd; transform: rotate(225deg); left: 8px; }

.items__top { margin-bottom: 30px; }

.items-top { border: 1px solid  #e7e7e7; padding: 10px 20px 10px 15px; }

@media only screen and (max-width: 1199px) { .items-top { display: block; font-size: 0; } }

.items-top__btns { font-size: 0; margin-right: 30px; display: inline-block; vertical-align: middle; }

@media only screen and (max-width: 1199px) { .items-top__btns { display: inline-block; vertical-align: middle; } }

.items-top__btn { display: inline-block; vertical-align: middle; font-size: 0; }

.items-top__btn:not(:last-child) { margin-right: 15px; }

.items-top__btn .icon { font-size: 16px; fill: #aeaeae; transition: fill .3s; }

.items-top__btn .icon-list { font-size: 25px; }

.items-top__btn.is-active .icon, .no-touch .items-top__btn:hover .icon { fill: #10ad44; }

.items-top__title { color: #aeaeae; font-family: 'helveticaneuecyr-light', Arial, sans-serif; font-size: 15px; display: inline-block; vertical-align: middle; }

@media only screen and (max-width: 1199px) { .items-top__title { display: inline-block; vertical-align: middle; } }

.items-top__selects { flex-grow: 1; display: flex; justify-content: flex-end; }

@media only screen and (max-width: 1199px) { .items-top__selects { justify-content: flex-start; padding-top: 10px; } }

@media only screen and (max-width: 575px) { .items-top__selects { display: block; } }

.items-top__select:last-child { margin-left: 30px; }

@media only screen and (max-width: 575px) { .items-top__select:last-child { margin-left: 0; } }

@media only screen and (max-width: 575px) { .items-top__select:not(:last-child) { margin-bottom: 10px; } }

.item { box-shadow: 0px 0px 12.92px 4.08px rgba(0, 0, 1, 0.15); margin-bottom: 30px; }

.item__content { padding: 12px 15px 5px; }

.item__img { background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; padding-top: 70%; background-color: #ccc; }

.item__content-top { display: flex; justify-content: space-between; align-items: center; margin-bottom: 10px; }

@media only screen and (max-width: 1199px) { .item__content-top { display: inline-flex; }
  .item__content-top .item-label { margin-right: 20px; } }

.item__video-link { font-size: 13px; color: #8d8d8d; transition: color .3s; }

.item__video-link .icon { fill: #8d8d8d; font-size: 14px; position: relative; top: 3px; }

.no-touch .item__video-link:hover { text-decoration: underline; color: #10ad44; }

.item__title { position: relative; padding-bottom: 10px; margin-bottom: 8px; }

.item__title::before { content: ''; position: absolute; bottom: 0; left: 0; height: 1px; background-color: #c1c1c1; width: 30px; }

.item__info { position: relative; margin-left: -15px; margin-right: -15px; padding-left: 15px; padding-right: 15px; border-bottom: 1px solid #f9f9f9; }

.item__price-lg { font-size: 21px; font-family: 'helveticaneuecyr-bold', Arial, sans-serif; color: #10ad44; }

.item__price-sm { font-family: 'helveticaneuecyr-light', Arial, sans-serif; color: #a7a7a7; font-size: 13px; }

.item__price { padding-top: 10px; padding-bottom: 10px; }

.item__btn { background-color: #2c2c2c; font-size: 15px; font-family: 'helveticaneuecyr-light', Arial, sans-serif; color: #fff; width: 100%; padding: 15px; display: flex; align-items: center; transition: background-color .3s; }

.no-touch .item__btn:hover { background-color: #10ad44; }

.no-touch .item__btn:hover .item__btn-arrow { width: 70px; }

.item__btn-title { margin-right: 10px; }

.item__btn-arrow { position: relative; width: 28px; display: block; transition: width .3s; }

.item__btn-arrow::before { width: 100%; content: ''; position: absolute; height: 1px; background-color: #fff; top: 50%; right: -1px; transform: translate(0, -50%); transition: width .3s; }

.item__btn-arrow::after { display: inline-block; position: absolute; width: 8px; height: 8px; border-left: 1px solid #fff; border-bottom: 1px solid #fff; transform: rotate(225deg); content: ''; top: 50%; transform: translate(0, -50%) rotate(225deg); right: 0; }

.item--row { position: relative; }

@media only screen and (max-width: 767px) { .item--row { padding-bottom: 51px; } }

.item--row .item-info-item { width: 33.33333%; }

@media only screen and (max-width: 575px) { .item--row .item-info-item { width: 50%; } }

.item--row .col-md-5 { position: static; }

.item--row .item__img { height: 100%; padding: 0; display: flex; flex-direction: column; justify-content: flex-end; }

@media only screen and (max-width: 767px) { .item--row .item__img { padding-top: 40%; } }

@media only screen and (max-width: 575px) { .item--row .item__img { padding-top: 50%; } }

@media only screen and (max-width: 767px) { .item--row .item__btn { position: absolute; z-index: 1; bottom: 0; left: 0; width: 100%; } }

.item--row .item__content { padding: 20px; padding-left: 0; padding-right: 25px; position: relative; }

@media only screen and (max-width: 767px) { .item--row .item__content { padding: 20px; } }

.item--row .item__content-top { display: inline-flex; }

.item--row .item__content-top .item-label { margin-right: 20px; }

.item--row .item__title { padding-bottom: 0; padding-right: 150px; }

@media only screen and (max-width: 1199px) { .item--row .item__title { padding-right: 0; } }

.item--row .item__price { position: absolute; top: 15px; right: 0; box-shadow: 0px 0px 4px 0px rgba(0, 0, 1, 0.11); text-align: center; padding: 2px 10px; }

@media only screen and (max-width: 575px) { .item--row .item__price { position: static; text-align: left; box-shadow: none; padding: 0; padding-top: 5px; } }

.item--row .item__text { font-size: 15px; color: #828282; text-align: justify; margin-bottom: 15px; }

.item--row .item__info { border-bottom-width: 0; }

@media only screen and (max-width: 575px) { .item--row .item__info { border-bottom-width: 1px; } }

.item-label { font-size: 11px; text-transform: uppercase; color: #fff; border-radius: 4px; padding: 4px 5px; display: flex; justify-content: center; min-width: 104px; align-items: center; }

.item-label.is-available { background-color: #10ad44; }

.item-label.is-intransit { background-color: #0073d5; }

.item-label.is-reserve { background-color: #ecac01; }

.item-label.is-sold { background-color: #dc002a; }

.item-label.is-ordered { background-color: #0073d5; }

.item-label__icon { font-size: 0; margin-right: 5px; }

.item-label__icon .icon { font-size: 11px; fill: currentColor; }

.item-label__icon .icon-truck { font-size: 15px; }

.item-info { font-size: 0; }

.item-info-item { font-size: 13px; color: #8d8d8d; display: inline-flex; width: 50%; margin-bottom: 7px; align-items: center; vertical-align: top; position: relative; padding-left: 30px; }

.item-info-item__icon { font-size: 0; margin-right: 7px; position: absolute; top: 0; left: 0; width: 25px; text-align: center; }

.item-info-item__icon .icon { fill: currentColor; font-size: 16px; }

.item-info-item__icon .icon-hatchback { position: relative; top: 4px; font-size: 22px; }

.selects__select { margin-bottom: 15px; }

.selects__btn { font-size: 0; }

.custom-select { position: relative; }

.custom-select select { visibility: hidden; opacity: 0; position: absolute; }

.custom-select.is-open .custom-select__panel { visibility: visible; opacity: 1; }

.custom-select.has-placeholder .custom-select__option:first-child { display: none; }

.custom-select__opener { height: 33px; border: 1px solid  #e8e8e8; font-family: 'helveticaneuecyr-light', Arial, sans-serif; font-size: 13px; padding: 7px 10px; padding-right: 35px; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; position: relative; color: #8d8d8d; cursor: pointer; }

.custom-select__opener::before { content: ''; display: inline-block; width: 7px; height: 7px; border-left: 1px solid #414141; border-bottom: 1px solid #414141; transform: rotate(-45deg); position: absolute; top: 11px; right: 15px; pointer-events: none; }

.custom-select__panel { visibility: hidden; opacity: 0; position: absolute; overflow: hidden; max-height: 300px; top: 100%; left: 0; width: 100%; background-color: #303030; z-index: 1; padding: 10px; }

.custom-select__option { font-family: 'helveticaneuecyr-light', Arial, sans-serif; font-size: 13px; color: #fff; padding-top: 5px; padding-bottom: 5px; cursor: pointer; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; vertical-align: middle; }

.no-touch .custom-select__option:hover { color: #10ad44; }

.custom-select__option label { pointer-events: none; }

.custom-select__option input[type="checkbox"] { display: inline-block; margin-right: 5px; position: relative; top: 3px; }

.custom-select__option .color-square { display: inline-block; margin-right: 5px; position: relative; top: 2px; height: 12px; width: 12px; background-color: red; }

.custom-select__option.is-selected { color: #10ad44; }

.custom-select__panel-inner { max-height: 300px; }

.custom-select__panel-item input { height: 32px; padding: 5px; width: 100%; background-color: #fff; font-family: inherit; }

.custom-select__panel-item input::placeholder { color: #979797; font-size: 15px; font-family: inherit; font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

.select--multiple .custom-select__opener { height: 48px; padding: 13px 20px; padding-right: 40px; font-size: 15px; color: #414141; }

.select--multiple .custom-select__opener::before { top: 17px; right: 20px; }

.select--multiple .custom-select__panel { padding: 20px; padding-top: 50px; max-height: none; }

.select--multiple .custom-select__panel-item { position: absolute; width: 100%; top: 20px; padding-left: 15px; padding-right: 15px; z-index: 1; left: 0; }

.select--multiple .custom-select__option { padding-top: 10px; padding-bottom: 10px; font-size: 15px; }

.items-top-select { display: flex; align-items: center; }

.items-top-select label { margin-right: 12px; font-size: 15px; }

.range-slider .noUi-handle { border-radius: 50%; width: 12px; height: 12px; background-color: #10ad44; border: 0; box-shadow: none; top: -5px; cursor: pointer; }

.range-slider .noUi-handle::before, .range-slider .noUi-handle::after { display: none; }

.range-slider .noUi-handle-lower { right: -8px !important; }

.range-slider .noUi-handle-upper { right: -8px !important; }

.range-slider .noUi-horizontal { height: 3px; box-shadow: none; border: 0; border-radius: 0; background-color: #414141; }

.range-slider .noUi-horizontal::before, .range-slider .noUi-horizontal::after { content: ''; position: absolute; top: -5px; display: inline-block; border-radius: 50%; width: 12px; height: 12px; background-color: #636363; pointer-events: none; }

.range-slider .noUi-horizontal::before { left: -4px; }

.range-slider .noUi-horizontal::after { right: -8px; }

.range-slider .noUi-connects { border-radius: 0; }

.range-slider .noUi-connect { border-radius: 0; background-color: #10ad44; cursor: pointer; }

.range-slider__title { font-family: 'helveticaneuecyr-light', Arial, sans-serif; font-size: 15px; margin-bottom: 5px; }

.breadcrumbs { font-size: 15px; }

.breadcrumbs__item { display: inline-block; }

.breadcrumbs__link { color: #fff; font-size: 15px; font-family: 'helveticaneuecyr-light', Arial, sans-serif; }

.breadcrumbs__link.is-active { color: #9b9b9b; pointer-events: none; }
